/* global */
html {
  height: 100%;
  width: 100%;
}

html,
body,
#root {
  display: flex;
  flex: 1
}

/* side bar */
#sideBar {
  transform: translateX(-150px);
  transition: transform 400ms ease-in;
  width: 300px;
  background-color: white;
  padding: 20px;
  z-index: 10;
}

#sideBar.slideIn {
  transform: translateX(0);
}

#sideBar.slideOut {
  transform: translateX(-100%);
}

.fa-heart {
  color:red !important;
}

.fa-heart, 
.fa-heart-o {
  font-size: 1.75em;
}
